@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  width: 100vw;
  padding: 0;
  margin: 0;
  background-image: linear-gradient(to right bottom, #12142715, #1c1c2b33);
}

.logo {
  margin-left: 5%;
}

h1 {
  margin: 0;

}

.app-body {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;

}

.sidebar {
  width: 300px;
  height: 100%;
  border-right: black 1px solid;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  z-index: 10;
  background-image: linear-gradient(to right bottom, #0f111f, #1c1c29);
  -webkit-filter: drop-shadow(0 0 0.5rem rgba(0, 0, 0, 0.76));
          filter: drop-shadow(0 0 0.5rem rgba(0, 0, 0, 0.76));
  border-radius: 0 30px 20px 0;
}

a {
  text-decoration: none; 

}

.title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  margin-top: 20px;
  font-size: 40px;
  color: white;
  font-family: 'Lobster', cursive;
}

.jot-list {
  margin-top: 10px;

}

.jot {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 60px;
  width: 220px;
  margin: 10px 0;
  background-image: linear-gradient(to right bottom, #ffffffe0, #ffffff4f);
  color: #121427;
  border-radius: 2rem;
  cursor: pointer;

}

.remove-btn {
  position: relative;
  display: none;
  cursor: pointer;
  position: absolute;
  right: 5%;

}

.remove-btn-active {
  display: -webkit-flex;
  display: flex;
  background-color: transparent;
  border: none;
  padding: 10px;
  color: rgba(0, 0, 0, 0.726);

}


.font-ellipsis {
  pointer-events: none;
  font-size: 17px;

}

.add-btn {
  all: unset;
  height: 35px;
  width: 220px;
  cursor: pointer;
  color: white;
  font-size: 22px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  letter-spacing: 4px;
}

.font-plus {
  margin-left: 8px;
}

.add-btn:hover {
  color: rgba(255, 255, 255, 0.767);
  transition: color 0.5s;

}

.jot-title {
  font-size: 18px;
  pointer-events: none;

}

.jot-date {
  font-size: 12px;
  pointer-events: none;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 80%;
  
}

.form-title {
  height: 50px;
  width: 60%;
  margin-bottom: 40px;
  padding: 0 2rem;
  border-radius: 10px;
    /* background-color: #1c1c2b28; */
  background-color: #ECE9E6;
    /* filter: drop-shadow(0 0 30px #333); */
  border: none;
  outline: none;
  color: #1c1c29;
  resize: none;
  overflow: hidden;
  font-size: 30px;
}

.form-text {
  height: 400px;
  width: 60%;
  border: rgb(160, 158, 158) solid 1px;
  border-radius: 5px;
  outline: none;
  color: black;
  resize: none;
  padding: 2rem;
  font-size: 18px;
}

.active {
  background-image: linear-gradient(to right bottom, #ffffffe0, #ffffffab);

}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}



